export const calculatorInit = () => {
  const gameContainer = document.querySelector('.calculator');

  const gameHtml = `
  <div class="calculator container">
    <h2 class="calculator__title">–ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä</h2>
    <div class="calculator__inner">
      <input
        type="number"
        class="calculator__input calculator__input--left"
        id="calc-input-1"
        placeholder="–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ"
        aria-label="–ü–µ—Ä—à–µ —á–∏—Å–ª–æ"
      />
      <div class="calculator__operators">
        <button
          type="button"
          class="calculator__operator"
          data-op="add"
          aria-label="–î–æ–¥–∞–≤–∞–Ω–Ω—è"
        >
        <span class="operator__span" aria-hidden="true">+</span> 
        </button>

        <button
          type="button"
          class="calculator__operator"
          data-op="subtract"
          aria-label="–í—ñ–¥–Ω—ñ–º–∞–Ω–Ω—è"
        >
        <span class="operator__span">‚àí</span>
        </button>

        <button
          type="button"
          class="calculator__operator"
          data-op="multiply"
          aria-label="–ú–Ω–æ–∂–µ–Ω–Ω—è"
        >
        <span class="operator__span">
        √ó
        </span>
        </button>
        
        <button
          type="button"
          class="calculator__operator"
          data-op="divide"
          aria-label="–î—ñ–ª–µ–Ω–Ω—è"
        >
        <span class="operator__span">/</span>
        </button>
      </div>
      <input
        type="number"
        class="calculator__input calculator__input--right"
        id="calc-input-2"
        placeholder="–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ"
        aria-label="–î—Ä—É–≥–µ —á–∏—Å–ª–æ"
      />
      <button
          type="button"
          class="calculator__operator"
          data-op="result"
          aria-label="–†–µ–∑—É–ª—å—Ç–∞—Ç"
        >
        <span class="operator__span">=</span>
        </button>
      
      <input
        type="text"
        class="calculator__result"
        id="calc-result"
        placeholder="–†–µ–∑—É–ª—å—Ç–∞—Ç"
        readonly
        aria-label="–†–µ–∑—É–ª—å—Ç–∞—Ç"
      />
    </div>
  </div>
  <hr class="divider" />
`;
  gameContainer.innerHTML = gameHtml;
};
const calc1 = document.getElementById('calc-input-1');
const calc2 = document.getElementById('calc-input-2');
const calcRes = document.getElementById('calc-result');
let op = null;

document.querySelectorAll('.calculator__operator').forEach(btn => {
  btn.addEventListener('click', () => {
    const action = btn.dataset.op;

    if (action === 'result') {
      const a = +calc1.value;
      const b = +calc2.value;
      let result = '';

      if (isNaN(a) || isNaN(b)) {
        result = '–ü–æ–º–∏–ª–∫–∞';
      } else if (op === 'add') {
        result = a + b;
      } else if (op === 'subtract') {
        result = a - b;
      } else if (op === 'multiply') {
        result = a * b;
      } else if (op === 'divide') {
        result = b !== 0 ? a / b : '‚àû';
      } else {
        result = '–û–±–µ—Ä—ñ—Ç—å –æ–ø–µ—Ä–∞—Ü—ñ—é';
      }

      calcRes.value = result;
    } else {
      op = action;
    }
  });
});
